export default {
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [{
				name: '动态'
			}, {
				name: '帖子'
			}, {
				name: '点赞'
			}, {
				name: '评论'
			}],

			swiperDateList: [
				[],
				[],
				[],
				[],
				[],
				[]
			],

			// 用于分页的属性
			totalPage: 1,
			page: 1,
			videoList: [],

			screenWidth: 350,
			serverUrl: "",
			
			thisUserInfo: '',
		}
	},

	changeIndicatorDots(e) {
		this.indicatorDots = !this.indicatorDots
	},
	changeAutoplay(e) {
		this.autoplay = !this.autoplay
	},
	intervalChange(e) {
		this.interval = e.target.value
	},
	durationChange(e) {
		this.duration = e.target.value
	},

	onLoad(opt) {
		var screenWidth = uni.getSystemInfoSync().screenWidth;
		this.screenWidth = screenWidth;
		
		// 获取当前分页
		var page = this.page;
		
		// 获取这个人的信息, TODO: 更新本地用户信息缓存
		var userId = opt.userId
		this.queryUserInfo(userId)
		
		// [测试代码块]
		this.$nextTick(function(){
			console.log(this.thisUserInfo);
			uni.setNavigationBarTitle({
				title: this.thisUserInfo.nickname + "的主页"
			});
		})
		
		// this.mySocket.init()
	},

	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
			if (this.currentTab == current) {
				return false;
			} else {
				this.currentTab = current;
				this.setScrollLeft(current);
			}
		},
		swiperChange: async function(e) {
			let index = e.target.current;
			this.setScrollLeft(index);
			this.currentTab = index;
		},
		setScrollLeft: async function(tabIndex) {
			let leftWidthSum = 0;
			for (var i = 0; i <= tabIndex; i++) {
				let nowElement = await this.getWidth('tabNum' + i);
				leftWidthSum = leftWidthSum + nowElement.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth;
			this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
		},
		getWidth: function(id) { //得到元素的宽高
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		loadMore: function(tabIndex) {
			console.log('正在加载更多数据。。。')
			this.getDateList(tabIndex);
		},
		/**
		 * 调用添加关注的接口
		 */
		addFollow: function(){
			
		},
		
		goToChatPage: function(){
			var friendInfo = this.thisUserInfo;
			uni.redirectTo({
				url: '../chatpage/chatpage?friendInfo=' + JSON.stringify(friendInfo),
			});
		},
		
		/**
		 * @param {Object} currentTab 0: 关注 1: 粉丝
		 */
		goToFansFollow: function(currentTab) {
			console.log("goToFansFollow...")
			var data={
				currentTab: currentTab,
				thisUserInfo: this.thisUserInfo
			}
			uni.redirectTo({
				url: '../followlist/followlist?data=' + JSON.stringify(data),
			});
		},
		
		queryUserInfo: function(userId){
			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/queryUser',
				method: "POST",
				data: {
					userId: userId
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					// console.log(res)
					if(res.data.status == 200){
						that.thisUserInfo = res.data.data;
						
					}
				}
			});
		}
	}
}
